import lk from "../images/lk.svg"
import coin from "../images/coin.png"
import Card from "./Card"
import React from "react"

function Lk(props) {
	const [cards, setCards] = React.useState(props.cards)
	const [isNowButtonActive, setNowButtonActive] = React.useState(true)
	const [isProgressButtonActive, setProgressButtonActive] = React.useState(null)

	function handleCards(filteredCards) {
		if (filteredCards === '') {
			setCards(props.cards, props.cardsProgress)
			return
		}
		setCards(filteredCards)
		handleChangeActive()
	}

	function handleChangeActive() {
		if (isNowButtonActive) {
			setNowButtonActive(false)
			setProgressButtonActive(true)
		} else if (isProgressButtonActive) {
			setProgressButtonActive(false)
			setNowButtonActive(true)
		}
	}

	React.useEffect(() => {
		handleCards('')
	}, [props.cards, props.cardsProgress])

	return (
		<div className="content">
			<div className="lk">
				<img className="lk__image" src={lk} alt="  " />
				<div className="lk__scroll-table">
					<table>
						<caption className="lk__scroll-table-title"> </caption>
						<thead>
							<tr>
								<th> </th>
								<th> </th>
								<th></th>
							</tr>
						</thead>
					</table>
					<div className="lk__scroll-table-body">
						<table>
							<tbody>
								<tr>
									<td className="lk__scroll-table-subject">   </td>
									<td>13.11.2023</td>
									<td>
										<div className="card__coins">
											<img src={coin} className="card__coins-icon" alt="" />
											<h3 className="card__coins-number">14</h3>
										</div>
									</td>
								</tr>
								<tr>
									<td className="lk__scroll-table-subject">   </td>
									<td>13.11.2023</td>
									<td>
										<div className="card__coins">
											<img src={coin} className="card__coins-icon" alt="" />
											<h3 className="card__coins-number">14</h3>
										</div>
									</td>
								</tr>
								<tr>
									<td className="lk__scroll-table-subject">   </td>
									<td>13.11.2023</td>
									<td>
										<div className="card__coins">
											<img src={coin} className="card__coins-icon" alt="" />
											<h3 className="card__coins-number">14</h3>
										</div>
									</td>
								</tr>
								<tr>
									<td className="lk__scroll-table-subject">   </td>
									<td>13.11.2023</td>
									<td>
										<div className="card__coins">
											<img src={coin} className="card__coins-icon" alt="" />
											<h3 className="card__coins-number">14</h3>
										</div>
									</td>
								</tr>
								<tr>
									<td className="lk__scroll-table-subject">   </td>
									<td>13.11.2023</td>
									<td>
										<div className="card__coins">
											<img src={coin} className="card__coins-icon" alt="" />
											<h3 className="card__coins-number">14</h3>
										</div>
									</td>
								</tr>
								<tr>
									<td className="lk__scroll-table-subject">   </td>
									<td>13.11.2023</td>
									<td>
										<div className="card__coins">
											<img src={coin} className="card__coins-icon" alt="" />
											<h3 className="card__coins-number">14</h3>
										</div>
									</td>
								</tr>
								<tr>
									<td className="lk__scroll-table-subject">   </td>
									<td>13.11.2023</td>
									<td>
										<div className="card__coins">
											<img src={coin} className="card__coins-icon" alt="" />
											<h3 className="card__coins-number">14</h3>
										</div>
									</td>
								</tr>
								<tr>
									<td className="lk__scroll-table-subject">   </td>
									<td>13.11.2023</td>
									<td>
										<div className="card__coins">
											<img src={coin} className="card__coins-icon" alt="" />
											<h3 className="card__coins-number">14</h3>
										</div>
									</td>
								</tr>
								<tr>
									<td className="lk__scroll-table-subject">   </td>
									<td>13.11.2023</td>
									<td>
										<div className="card__coins">
											<img src={coin} className="card__coins-icon" alt="" />
											<h3 className="card__coins-number">14</h3>
										</div>
									</td>
								</tr>
								<tr>
									<td className="lk__scroll-table-subject">   </td>
									<td>13.11.2023</td>
									<td>
										<div className="card__coins">
											<img src={coin} className="card__coins-icon" alt="" />
											<h3 className="card__coins-number">14</h3>
										</div>
									</td>
								</tr>
								<tr>
									<td className="lk__scroll-table-subject">   </td>
									<td>13.11.2023</td>
									<td>
										<div className="card__coins">
											<img src={coin} className="card__coins-icon" alt="" />
											<h3 className="card__coins-number">14</h3>
										</div>
									</td>
								</tr>
							</tbody>
						</table>
					</div>	
				</div>
			</div>
			<div className="filter__buttons">
				<button type="button" className={`filter__buttons_now ${isNowButtonActive ? 'filter__buttons_now_active' : ''}`} onClick={()=>handleCards(props.cards)}> </button>
				<button type="button" className={`filter__buttons_progress ${isProgressButtonActive ? 'filter__buttons_progress_active' : ''} `} onClick={()=>handleCards(props.cardsProgress)}>  </button>
				<button type="button" className="filter__buttons_done"> </button>
				<button type="button" className="filter__buttons_canceled">  </button>
			</div>
			{cards.length === 0 && <p className="nocards">     </p>}
			<section className="cards">
				{cards?.map(card => {
						return (
							<Card
								card={card}
								key={card.id}
								onClick={props.onClick}
								deleteCard={props.deleteCard}
								button_title=""
								todo={true}
								buttonPopup = ""
								cardsProgress={props.cardsProgress}
							/>
						)
					})}
			</section>
		</div>
	)
}

export default Lk;