function InfoTooltip(props) {
	return (
		<section className={`popup ${props.isOpen ? 'popup_opened' : ''}`}>
			<div className="popup__container">
				<div className="popup__sign">
					<img className="popup__image" alt=" " src={props.image} />
					<button className="popup__close-button" type="button" aria-label=" " onClick={props.onClose}></button>
					<h2 className="popup__alert">{props.title}</h2>
				</div>
			</div>
		</section>	
	)
}

export default InfoTooltip;